<template>
	<div id="love-heart">
		<div class="header">
			<div class="header-title">
				<img class="lt-arrow" src="../../../assets/arrow.png" @click="$router.go(-1)"/>
				<span class="title">我的爱心</span>
			</div>
			<div class="header-info">
				<div class="info-left">
					<div class="info-title">
						<em>{{balanceTotal}}</em>爱心
					</div>
					<div class="info-text">爱心可用于兑换虚拟礼物</div>
					<div class="info-right">
						<div class="recharge" @click="linkTo('HeartRecharge')">爱心充值</div>
					</div>
				</div>
			</div>
		</div>
		<div class="body">
			<div class="body-title">爱心明细</div>
			<div class="body-list">
				<div class="list-item" v-for="(item,index) in accountLogs">
					<div class="list-title">{{item.typeName}}</div>
					<div class="list-time">{{item.transTime}}</div>
					<span class="list-num active">{{item.amount}}</span>
				</div>
			</div>
		</div>
		<div class="footer">
			<div class="footer-num">
				<span>数量</span>
				<input type="number" value="0" class="needsclick"/>
			</div>
			<div class="footer-btn">兑换零钱</div>
		</div>
	</div>
</template>

<script>
const LOVEHEART = 13
export default {
	data(){
		return {
			accountLogs:[],
			balanceTotal:0,
		}
	},
  components: {
  	
  },
  created(){
  	this.getMyAccountList()
  	this.getAccountDetailList()
  },
  methods:{
  	getMyAccountList(){
  		this.$Api.getMyAccountList(LOVEHEART).then((res)=>{
  			if(res.q.s == 0){
  				this.balanceTotal = res.q.Accounts[0].balanceTotal
  			}
  		})
  	},
  	getAccountDetailList(){
  		this.$Api.getAccountDetailList(LOVEHEART).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.accountLogs = res.q.accountLogs
  			}
  		})
  	}
  },
}
</script>

<style lang="scss">
	@import "../../../sass/baseSass.scss";
	#love-heart{
		height: 100%;
		background: #f0f0f0;
		.header{
			width: 100%;
			height: 3.73rem;
		  color: #fff;
			@include bg-color;
			.header-title{
		    width: 100%;
		    height: 1.17rem;
		    line-height: 1.17rem;
		    padding: 0 0.4rem;
		    text-align:center;
				position: relative;
				.lt-arrow{
					display: inline-block;
					@include lt-arrow;
				}
				.title{
					font-size: 0.48rem;
				}
				.header-right{
					position: absolute;
					font-size: 0.4rem;
					right: 0.4rem;
				}
			}
			.header-info{
				padding:0.4rem 0.4rem 0 0.51rem; 
				.info-left{
					position: relative;
					line-height: 1;

					.info-title{
						font-size: 0.43rem;
						>em{
							font-size: 0.77rem;
							margin-right: 0.13rem;
							
						}
					}
					.info-text{
						font-size: 0.35rem;
						padding-top: 0.24rem;
					}
				}
				.info-right{
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					right: 0;
					.recharge{
						width: 2.4rem;
						height: 0.96rem;
						line-height: 0.96rem;
						border-radius: 0.48rem;
						background: #fff;
						text-align: center;
						color: $label-color;
						background: #fff;
					}
				}
			}

		}
		.body{
			.body-title{
				font-size: 0.43rem;
				height: 1.2rem;
				line-height: 1.2rem;
				padding: 0 0.4rem;
				color: #1a1a1a;
			}
			.body-list{
				background: #fff;
				.list-item{
					position: relative;
					line-height: 1;
					padding: 0.4rem;
					border-bottom: 1px solid $border-color;
				}
				.list-title{
					font-size: 0.4rem;
					color: #333;
				}
				.list-time{
					font-size: 0.37rem;
					padding-top: 0.24rem;
					color: #666;
				}
				.list-num{
					position: absolute;
					right: 0.4rem;
					top: 50%;
					transform: translateY(-50%);
					font-size: 0.53rem;
					color: #333;
				}
				.active{
					color: #de7fef;
				}
			}
		}
		.footer{
			position: fixed;
			bottom: 0;
			width: 100%;
			height: 1.31rem;
			line-height: 1.31rem;
			border-top: 1px solid $border-color;
			background: #fff;
			.footer-num{
				float: left;
				padding-left: 0.4rem;
				font-size: 0.37rem;
				color: #1a1a1a;
				>span{
					margin-right: 0.67rem;
				}
				input{
					font-size: 0.48rem;
					color: #333;
					width: 1.89rem;
  				height: 0.83rem;
  				line-height: 0.83rem;
  				text-align: center;
  				border: 1px solid $border-color;
				}
			}
			.footer-btn{
				float: right;
				font-size: 0.45rem;
				width: 4.36rem;
				height: 1.31rem;
				line-height: 1.31rem;
				color: #fff;
				text-align: center;
				@include bg-color;
			}
		}
	}
</style>


